*{
   box-sizing: border-box;
   margin: 0;
   padding: 0;
}

.DashboardContainer{
   height: 100vh;
}

/* Navbar */
nav{
   background-color: #bdbdbd;
   border-bottom: 2px solid black;
   color: white;
   font-size: 1.3rem;
   height: 7%;
   padding: 0 1rem;

   display: flex;
   justify-content: space-between;
   align-items: center;
}
nav button{
   background: #f44336;
   border: 2px solid #d32f2f;
   color: white;
   cursor: pointer;
   font-size: .9rem;
   padding: .3rem;

   display: flex;
   justify-content: center;
   align-items: center;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}

/* Section */
section{
   border: 1px solid black;
   width: calc(100% - 2rem);
   height: calc(93% - 2rem);
   margin: 1rem auto 0 auto;

   border-radius:.6rem;
   -webkit-border-radius:.6rem;
   -moz-border-radius:.6rem;
   -ms-border-radius:.6rem;
   -o-border-radius:.6rem;
}

/* Section Header */
.section-header{
   border-bottom: 1px solid gray;
   height: 7%;

   display: flex;
   justify-content: space-evenly;
   align-items: center;
}
.section-header *{
   font-size: 1rem;
   padding: 0 .3rem;
   height: 90%;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}
.section-header button{
   background-color: #28a745;
   color: white;
   cursor: pointer;
}
.section-header input{
   width: 40%;
}

/* Articles */
.articles{
   height: 93%;
   padding: 1rem 0;
   
   overflow-y: scroll;
   -ms-overflow-style: none;
   scrollbar-width: none;  

   display: flex;
   justify-content: space-evenly;
   flex-wrap: wrap;
   gap: 1rem 0;
}
.articles::-webkit-scrollbar {
   display: none;
}

/* Article */
.article{
   box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
   cursor: pointer;
   overflow: hidden;
   padding: 1rem;

   height: 30vh;
   width: 30%;

   border-radius: .6rem;
   -webkit-border-radius: .6rem;
   -moz-border-radius: .6rem;
   -ms-border-radius: .6rem;
   -o-border-radius: .6rem;
}
.article div{
   display: flex;
   justify-content: center;
}
.article div:nth-child(1){
   font-size: .7rem;
   height: 10%;
}
.article div:nth-child(2){
   text-align: center;
   height: 20%;
}
.article div:nth-child(3){
   height: 70%;
   overflow: hidden;
}



/* INDIVIDUAL ARTICLE */

/* Header */
.individual-article{
   border: 1.5px solid black;

   display: flex;
   flex-direction: column;
}
.header{
   border-bottom: 1px solid black;
   padding-right: .3rem;
   height: 7%;

   display: flex;
   justify-content: flex-end;
   align-items: center;
}
.header button{
   background-color: #dc3545;
   color: white;
   cursor: pointer;
   font-size: 1.3rem;
   font-weight: 900;
   padding: .5rem;

   height: 4.5vh;
   width: 4.5vh;
   
   display: flex;
   justify-content: center;
   align-items: center;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}

/* Content */
.content{
   height: 93%;
}
.title{
   border-bottom: 1.5px solid black;
   height: 7%;
   display: flex;
}
.title .left{
   border-right: 1.5px solid black;
   font-size: 1.3rem;
   width: 20%;

   display: flex;
   justify-content: center;
   align-items: center;
}
.title .right{
   border: none;
   font-size: 1rem;
   resize: none;
   flex-grow: 1;

   padding: .6rem;

   overflow-y: scroll;
   -ms-overflow-style: none;
   scrollbar-width: none;  
}
.title .right::-webkit-scrollbar {
   display: none;
}

.picture{
   border-bottom: 1.5px solid black;
   display: flex;
   height: 13%;
}
.picture .left{
   width: 60%;
   display: flex;
}
.picture .left > div:nth-child(2) button{
   color: white;
   cursor: pointer;
   font-size: .9rem;
   padding: .3rem;

   display: flex;
   justify-content: center;
   align-items: center;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}
.picture .left > div:nth-child(2) button:nth-child(2){
   background: #f44336;
   border: 2px solid #d32f2f;
}
.picture .left > div:nth-child(2) button:nth-child(3){
   background-color: #28a745;
   border: 2px solid #49bb4c;
}

.new-article .picture .left{
   display: block;
   
   display: flex;
   justify-content: space-evenly;
   flex-wrap: wrap;
}
.editor-picture-left-div1{
   border-right: 1.5px solid black;
   gap: 1rem;
   padding: 1rem;
   
   display: flex;
   align-items: center;
}
.new-article .picture .left > div:nth-child(1){
   border: 1.5px solid black;
   height: 1.3rem;
   font-size: .9rem;
   text-align: center;
   width: 100%;

   display: flex;
   justify-content: center;
   align-items: center;
}
.picture .left > div:nth-child(1) *{
   padding: .5rem;
}
.picture .left > div:nth-child(2){
   flex-grow: 1;

   display: flex;
   flex-direction: column;
}
.picture .left > div:nth-child(2) div:nth-child(1){
   border-bottom: 1px solid black;
   height: 1.3rem;
   font-size: .9rem;
   text-align: center;

   display: flex;
   justify-content: center;
   align-items: center;
}
.new-article .picture .left > div:nth-child(1){
   border: none;
   border-bottom: 1px solid black;
}
.picture .left > div:nth-child(2) div:nth-child(2){
   flex-grow: 1;

   display: flex;
   justify-content: space-evenly;
   align-items: center;
}
.picture .left input{
   border: 1px solid black;
   width: 50%;
}
.picture .left button{
   color: #28a745;
   cursor: pointer;
   font-weight: 500;
   
   display: flex;
   justify-content: center;
   align-items: center;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}
.picture .left button:hover{
   background-color: #28a745;
   color: white;
}

.picture .right{
   border-left: 1.5px solid black;
   width: 40%;

   display: flex;
   justify-content: space-evenly;
}
.new-article .picture .right{
   width: 50%;
}
.picture .right button{
   cursor: pointer;
   margin: .3rem;
   width: 30%;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}
.picture .active{
   background-color: #28a745;
   color: white;
}
.picture .right button:hover{
   background-color: #28a745;
   color: white;
}

.content div:nth-child(3){
   height: 80%;
   /* padding: .5rem; */

   display: flex;
   justify-content: center;
   align-items: center;
}
.text{
   border: none;
   font-size: 1rem;
   padding: .5rem;
   resize: none;

   height: 100%;
   width: 100%;


   align-self: flex-start;
   justify-self: flex-start;
}
.image{
   max-height: 100%;
   max-width: 100%;
}

/* Footer */
.footer{
   border-top: 1.5px solid black;
   padding: .5rem;
   
   display: flex;
   justify-content: flex-end;
}
.footer *{
   color: white;
   cursor: pointer;
   font-size: 1rem;
   padding: .5rem;

   border-radius: .3rem;
   -webkit-border-radius: .3rem;
   -moz-border-radius: .3rem;
   -ms-border-radius: .3rem;
   -o-border-radius: .3rem;
}

.footer button:nth-child(1){
   background-color: #28a745;
}
.footer button:nth-child(2){
   background-color: #dc3545;
   margin-left: .5rem;
}
.message404{
   background-color: #9e9e9e;
   color: white;
   height: 100vh;
   font-size: 3rem;
   font-weight: 700;

   display: flex;
   justify-content: center;
   align-items: center;
}